.Banner01 {
  background-color: var(--clr-bg);
}

.Banner01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(2, calc(50% - 10rem));
  gap: 20rem;
  justify-content: center;
  align-items: center;
}

.Banner01-box > div:nth-child(1) {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}

.Banner01-box > div:nth-child(2) {
  width: 80%;
  position: relative;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}
.Banner01-box > div:nth-child(2) > img {
  position: relative;
  z-index: 1;
  width: 100%;

  background-color: var(--clr-bg);
}
.Banner01-box > div:nth-child(2) > div {
  position: relative;
  z-index: 2;
  width: 80%;
  margin-top: -5rem;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  background-color: var(--clr-2);
}
/*-- responsive --*/

@media screen and (max-width: 1480px) {
  .Banner01-box {
    grid-template-columns: repeat(2, calc(50% - 5rem));
    gap: 10rem;
  }
}

@media screen and (max-width: 1000px) {
  .Banner01-box {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: start;
    align-items: start;
  }
  .Banner01-box > div:nth-child(2) {
    width: 100%;
    position: relative;
  }
}
