.About01 {
  padding-top: 15rem;
}

.About01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(2, calc(50% - 5rem));
  gap: 10rem;
  justify-content: center;
  align-items: center;
}

.About01-box > div {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}
.About01-box > img {
  width: 80%;
  aspect-ratio: 1/1;
  justify-self: center;

  background-color: var(--clr-3);
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
  .About01 {
    padding-top: 10rem;
  }
  .About01-box > img {
    width: 100%;
  }
}

@media screen and (max-width: 1000px) {
  .About01 {
    padding-top: 3rem;
  }
  .About01-box {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: start;
    align-items: start;
  }
  .About01-box > img {
    width: 100%;
  }
}
